import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { AuthService } from '../auth/auth.service';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

const user = "1c1b28f2-a84b-4b30";
const password = "VJezNXV9Wz";

@Injectable({
  providedIn: 'root'
})
export class PaiementService {
  url = 'https://test-api.centralpay.net/v2/rest/paymentRequest'
  endpoint = environment.endpoint

  constructor(private http: HttpClient, private authService: AuthService) { }

  payPack(email: string, amount: number) {
    this.authService.paying = true;
    const breakdown = "{\"amount\": " + amount + " ,\"email\":" + email +"}"
      const data = {
        "paymentMethod[]": "TRANSACTION",
        "currency": "EUR",
        "totalAmount": amount,
        "breakdown[]": breakdown
      }
    this.http.post(`${this.url}`, data, httpOptions).subscribe(
        (data) => {
          console.log(data)
          this.authService.paying = false;
        }, (error) => {
          console.log(error)
          this.authService.paying = false;
        }
      )
  }


  buyPack(credit: number) {
    const data = {
      credit
    }
    return this.http.post(`${this.endpoint}/paiement/buy-pack`, data, httpOptions)
  }
}
